}

.nav {
  color: whitesmoke;
  border-bottom: 2px solid rgb(255, 123, 0);
  min-width: 400px;
}

.logo {
  margin-left: 5px;
}
.nav_ul {
  display: flex;
  list-style-type: none;
  justify-content: space-evenly;
  -webkit-padding-start: 0px;
          padding-inline-start: 0px;
  margin-bottom: 5px;
  margin-top: 10px;
}

.nav_item {
  transition: all .2s ease-in-out;
  color: gray;
  font-weight: bold;
  font-style: oblique;
  padding: 5px 10px;
  text-decoration: none;
  border-radius: 10px 10px 0px 0px;
}

.nav_item:hover {
  color: #111;
  background: rgb(255, 123, 0);
}

.small_nav {
  font-size: .8em;
  padding: 2px 5px;
  border-radius: 0px 0px 5px 5px;
  margin-top: 0px;
}

a.visited {
  color: white;
}
.clear_padding {
  padding: 0px;
  margin: 0px;
}

.main_wrapper {
  width: 100%;
  margin: auto;
  /* background: black; */
}

.court_wrapper {
  margin: auto;
  width: 380px;
  position: relative;
}

.court_text {
  color: #111;
  position: absolute;
  width: 250px;
  background-color: rgba(255, 255, 255, 0.5);
  border: 2px solid white;
  border-radius: 10px;
  padding: 5px 10px;
  z-index: 10;
  top: 4%;
  left: 50%;
  -webkit-transform: translate(-50%, 0);
  font-family: Arial, Helvetica, sans-serif;
  box-shadow: 0px 0px 10px 2px rgba(71, 50, 9, 0.5);
}

.spot {
  position: absolute;
  background: rgba(255, 166, 1, 0.4);
  margin-top: -7px;
  margin-left: -7px;
  width: 10px;
  height: 10px;
  border-radius: 50%;
  border: 2px solid white;
  padding: 0px;
  cursor: pointer;
}

.active, .spot:hover {
  background:rgb(4, 204, 14, 1.0);
  -webkit-transform: scale(1.2);
          transform: scale(1.2);
  box-shadow: 0px 0px 10px 2px rgba(17,17,17,0.5);
}

