.ROOKIE {
  -webkit-animation: rookieglow .8s infinite alternate;
          animation: rookieglow .8s infinite alternate;
}
@-webkit-keyframes rookieglow {
  to {
      text-shadow: 0 0 10px rgb(0, 238, 255);
  }
}
@keyframes rookieglow {
  to {
      text-shadow: 0 0 10px rgb(0, 238, 255);
  }
}

.SOPHOMORE {
  -webkit-animation: sophomoreglow .8s infinite alternate;
          animation: sophomoreglow .8s infinite alternate;
}
