.clear_padding {
  padding: 0px;
  margin: 0px;
}

.main_wrapper {
  width: 100%;
  margin: auto;
  /* background: black; */
}

.court_wrapper {
  margin: auto;
  width: 380px;
  position: relative;
}

.court_text {
  color: #111;
  position: absolute;
  width: 250px;
  background-color: rgba(255, 255, 255, 0.5);
  border: 2px solid white;
  border-radius: 10px;
  padding: 5px 10px;
  z-index: 10;
  top: 4%;
  left: 50%;
  -webkit-transform: translate(-50%, 0);
  font-family: Arial, Helvetica, sans-serif;
  -webkit-box-shadow: 0px 0px 10px 2px rgba(71, 50, 9, 0.5);
  -moz-box-shadow: 0px 0px 10px 2px rgba(71, 50, 9, 0.5);
  box-shadow: 0px 0px 10px 2px rgba(71, 50, 9, 0.5);
}

.spot {
  position: absolute;
  background: rgba(255, 166, 1, 0.4);
  margin-top: -7px;
  margin-left: -7px;
  width: 10px;
  height: 10px;
  border-radius: 50%;
  border: 2px solid white;
  padding: 0px;
  cursor: pointer;
}

.active, .spot:hover {
  background:rgb(4, 204, 14, 1.0);
  transform: scale(1.2);
  -webkit-box-shadow: 0px 0px 10px 2px rgba(17,17,17,0.5);
  -moz-box-shadow: 0px 0px 10px 2px rgba(17,17,17,0.5);
  box-shadow: 0px 0px 10px 2px rgba(17,17,17,0.5);
}

